import React from "react";
import { Box, Typography } from "@mui/material";
import footerLogo from "../assets/Convatec-footer-logo.png";

const Footer = () => {
  return (
    <Box
      sx={{
        flex: '0 0 2%',
        display: "flex", // Flexbox for aligning items horizontally
        justifyContent: "space-between", // Space between left and center content
        alignItems: "center", // Vertically center items
        padding: "10px", // Padding for the footer
        backgroundColor: "white", // Background color of the footer
        color: "#aa0061", // Text color
        width: '100%', // Full width of the viewport
        flexDirection: { xs: "column", sm: "row" }, // Stack items vertically on small screens, horizontally on larger screens
        textAlign: "center", // Center text content when items are stacked vertically
        maxHeight: "50px",
      }}
    >
      <img src={footerLogo} alt="Logo" height = '40px' style={{padding: '5px'}} />
      {/* Message in the center */}
      <Typography variant="caption" sx={{ flexGrow: 1, fontSize: '10px' }}>
      2024 Convatec | Confidential and Proprietary
      </Typography>
    </Box>
  );
};

export default Footer;
