import React, { useState } from 'react';
import '../../styles/DragDrop.css';
import Barrier from '../../assets/barrier.png';
import Homeostsis from '../../assets/homeostsis.png';
import Defence from '../../assets/defence.png';
import Endocrine from '../../assets/endocrine.png';
import Sensory from '../../assets/sensory.png';
import Healthindicator from '../../assets/healthindicator.png';
import { Box, Typography } from "@mui/material";

const images = [
  { id: 1, name: 'Barrier Function', url: Barrier, type: 'landscape', flipData: 'The skin protects the body by keeping out germs, controlling water loss, shielding it from injuries, and guarding against UV damage.'},
  { id: 2, name: 'Regulation of Homeostasis', url: Homeostsis, type: 'portrait', flipData : 'The skin regulates temperature, water balance, and heat through blood vessels and sweat glands, controlling water absorption and evaporation.'},
  { id: 3, name: 'Immunological Defence', url: Defence, type: 'landscape', flipData : 'The skin is vital for adaptive and innate immunity, protecting against infections, even from minor cuts.'},
  { id: 4, name: 'Endocrine and Exocrine Function', url: Endocrine, type: 'landscape', flipData : 'Exocrine regulates body temperature and Endocrine makes vitamin D when exposed to sunlight,' },
  { id: 5, name: 'Sensory Function', url: Sensory, type: 'landscape', flipData : 'The skin contains receptors for touch, pain, and temperature, helping us interact with the environment.'},
  { id: 6, name: 'Health Indicators', url: Healthindicator, type: 'landscape', flipData : 'The indicators like pigmentation, smoothness, and elasticity reflect overall health.'},
];

const names = ['Barrier Function', 'Regulation of Homeostasis', 'Immunological Defence', 'Endocrine and Exocrine Function', 'Sensory Function', 'Health Indicators'];

const Question1 = () => {
    const [imageList, setImageList] = useState(images);
    const [nameList, setNameList] = useState(names);
  
    const handleDrop = (imageId, draggedName, flipData) => {
      // Find the image by id
      const image = imageList.find((img) => img.id === imageId);
  
      if (image.name === draggedName) {
        // Flip image
        const updatedImages = imageList.map((img) =>
          img.id === imageId ? { ...img, flipped: true, matched: flipData } : img
        );
        setImageList(updatedImages);
  
        // Remove name from nameList
        const updatedNames = nameList.filter((name) => name !== draggedName);
        setNameList(updatedNames);
      } else {
        // If name does not match, reset its position
        alert('Try again! Function does not match.');
      }
    };
  
    const handleDragStart = (event, draggedName) => {
      event.dataTransfer.setData('name', draggedName);
    };
  
    const handleDragOver = (event) => {
      event.preventDefault();
    };
  
    const handleDropOnImage = (event, imageId, flipData) => {
      event.preventDefault();
      const draggedName = event.dataTransfer.getData('name');
      handleDrop(imageId, draggedName, flipData);
    };
  
    return (
      <div className="main-container">
        <Typography variant="h6" sx={{ marginBottom: 2, fontWeight: 600 }}>
        1. Drag and drop the correct functions title on to the respective images to get the function descriptions
      </Typography>
      <Box sx={{
        display:'flex',
        justifyContent:'space-evenly'
      }}>
      <div className="image-layout">
          {imageList.map((image, index) => (
            <div
              key={image.id}
              className={`image-container ${image.flipped ? 'flipped' : ''}`}
              onDrop={(event) => handleDropOnImage(event, image.id, image.flipData)}
              onDragOver={handleDragOver}
              style={{
                gridColumn: image.type === 'portrait' ? '2' : index % 3 === 0 ? '1' : '3',
                gridRow: image.type === 'portrait' ? 'span 3' : 'span 1',
              }}
            >
              <div className='image' style={{ backgroundImage: `url(${image.flipped? '' : image.url})` }} />
              {image.flipped && <div className="name-on-image">{image.matched}</div>}
            </div>
          ))}
        </div>
        <div className="names">
          {nameList.map((name) => (
            <div
              key={name}
              className="name"
              draggable
              onDragStart={(event) => handleDragStart(event, name)}
            >
              {name}
            </div>
          ))}
        </div>
      </Box>
        
      </div>
    );
  };
  

export default Question1;
