import React, { useState } from "react";
import Box from "@mui/material/Box";
import {
  Typography,
  RadioGroup,
  FormControl,
  FormControlLabel,
  Radio,
  Button,
} from "@mui/material";

const Question3 = ({ qno, answerStatus, setQNo, setAnswerStatus }) => {
  const [selectedOption, setSelectedOption] = useState(null);
  const options = [
    { value: "A", label: "True" },
    { value: "B", label: "False" },
  ];
  const correctAnswer = "B";

  const handleNext = () => {
    setQNo(qno + 1);
  };

  const handleOptionChange = (event) => {
    const option = event.target.value;
    setSelectedOption(option);
    setAnswerStatus((prevState) => ({
      ...prevState,
      [qno]: option === correctAnswer ? "correct" : "wrong",
    }));
  };

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "flex-start",
        padding: 2,
        width: "100%",
      }}
    >
      <Typography variant="h6" sx={{ marginBottom: 2, fontWeight: 600 }}>
      3. Epidermis houses the following: Lymph vessels, Collagen bundles, Nerves, Sebaceous glands?
      </Typography>
      <Typography variant="body1" sx={{ marginBottom: 2 }}>
        Select the correct answer:
      </Typography>
      <FormControl component="fieldset" sx={{ width: "100%" }}>
        <RadioGroup value={selectedOption} onChange={handleOptionChange}>
          {options.map((option, index) => (
            <FormControlLabel
              key={index}
              value={option.value}
              control={<Radio />}
              label={option.label}
            />
          ))}
        </RadioGroup>
      </FormControl>

      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "flex-end",
          width: "100%",
        }}
      >
        <Button
          variant="contained"
          color="primary"
          onClick={handleNext}
          sx={{ marginTop: 2 }}
          disabled={!answerStatus[qno]}
        >
          Next
        </Button>
      </Box>
    </Box>
  );
};

export default Question3;
