import React, { useState } from 'react';
import '../../common/styles/Popup.css';
import DragDrop from '../components/videoQuestions/Question1';
// import MatchTheFollowing from '../Course/Components/MatchTheFollowing';
// import RotatingImage from '../Course/Components/RotatingImage';

const Popup = ({ isOpen, handleProceed, mcq, isDragDrop, isRotateImage, isMatch }) => {
  const [selectedOption, setSelectedOption] = useState(null);

  const handleOptionClick = (option) => {
    setSelectedOption(option);
  };

  const validateSelectedOption = (selectedOption) => {
    return selectedOption === mcq?.answer ? 'correct' :  'wrong';
  }

  return (
    isOpen && (
      <div className="popup-overlay">
        <div className="popup-content">
          {mcq && (<><h2 className="popup-title">Question</h2><p className="popup-question">{mcq?.question}</p><div className="popup-options">
            {mcq?.options?.map((option, index) => (
              <div
                key={index}
                className={`popup-option ${selectedOption === option ? validateSelectedOption(selectedOption) : ''}`}
                onClick={() => handleOptionClick(option, index)}
              >
                {option}
              </div>
            ))}
          </div></>)}
          {isDragDrop && (<DragDrop />)}
          {/* {isRotateImage && (<RotatingImage />)}
          {isMatch && (<MatchTheFollowing />)} */}
          <button className="popup-proceed" onClick={handleProceed}>Proceed</button>
        </div>
      </div>
    )
  );
};

export default Popup;
