import React, { useState } from "react";
import Box from "@mui/material/Box";
import Navbar from "./Navbar";
import TitleBar from "./TitleBar";
import BodyContainer from "./BodyContainer";
import Footer from "./Footer";
import { Divider } from "@mui/material";
import overview from "../assets/overview.mp4";
import skinanatomy from "../assets/skinanatomy.mp4";

const drawerWidth = 240;

const defaultCourseDetails = {
  menuItems: [
    {
      type: "single",
      progress: 0,
      headerTitle: "Module Overview",
      videoContent: overview,
      displayName: "Overview",
      quiz: false,
    },
    {
      type: "multiple",
      progress: 0,
      videoContent: skinanatomy,
      displayName: "Skin Anatomy",
      quiz: true,
      quizCompleted: false,
      quizScore: 0,
      subItems: [
        {
          timeline: 114,
          displayName: "Introduction",
          headerTitle: "Introduction",
          supText: "1",
          reference: ['Yousef H, Alhajj M, Fakoya AO, Sharma S. Anatomy, Skin (Integument), Epidermis. In: StatPearls. StatPearls Publishing; 2024. Accessed October 3, 2024. http://www.ncbi.nlm.nih.gov/books/NBK470464/.'],
        },
        {
          timeline: 234,
          displayName: "Primary Functions of Skin",
          headerTitle: "Primary Functions",
          supText: "1",
          reference: ['Yousef H, Alhajj M, Fakoya AO, Sharma S. Anatomy, Skin (Integument), Epidermis. In: StatPearls. StatPearls Publishing; 2024. Accessed September 13, 2024.'],
        },
        {
          timeline: 310,
          displayName: "Layers of the Skin",
          headerTitle: "Layers of the Skin",
          supText: "1-3",
          reference: ['Anatomy of the Skin - Health Encyclopedia - University of Rochester Medical Center. Accessed September 11, 2024. https://www.urmc.rochester.edu/encyclopedia/content.aspx?contenttypeid=85&contentid=P01336',
            'Yousef H, Alhajj M, Fakoya AO, Sharma S. Anatomy, Skin (Integument), Epidermis. In: StatPearls. StatPearls Publishing; 2024. Accessed September 13, 2024. http://www.ncbi.nlm.nih.gov/books/NBK470464/',
            'Image (s) used under license from Shutterstock.com - SC-57352-SWE-ENG-v1 (v1.1)'
          ],
        },
        {
          timeline: 317,
          displayName: "Key Takeaways",
          headerTitle: "Key Takeaways",
          supText: "1-2",
          reference: ['Wong R, Geyer S, Weninger W, Guimberteau JC, Wong JK. The dynamic anatomy and patterning of skin. Experimental Dermatology. 2016;25(2):92-98. doi:10.1111/exd.12832', 
            'Anatomy of the Skin - Health Encyclopedia - University of Rochester Medical Center. Accessed September 11, 2024. https://www.urmc.rochester.edu/encyclopedia/content.aspx?contenttypeid=85&contentid=P01336'],
        },
      ],
      timelineProgress:{
        114: { progress: 0 },
        234: { progress: 0 },
        310: { progress: 0 },
        317: { progress: 0 },
      },
    }
  ],
};

const Landing = () => {
  const [open, setOpen] = useState(true);
  const [selectedMenuIndex, setSelectedMenuIndex] = useState(0);
  const [courseDetails, setCourseDetails] = useState(defaultCourseDetails);
  const [quiz, setQuiz] = useState(false);

  const handleDrawerToggle = () => {
    setOpen(!open);
  };

  const bodyContainerWidth = open ? `calc(100% - ${drawerWidth}px)` : "100%";

  return (
    <Box
      sx={{
        display: "flex",
        minHeight: "100vh",
        maxHeight: "100vh",
        backgroundColor: "#fafafa;",
        overflowY: "auto",
        overflowX: "hidden",
      }}
    >
      {open && (
        <Navbar
          drawerWidth={drawerWidth}
          open={open}
          courseDetails={courseDetails}
          setCourseDetails={setCourseDetails}
          setSelectedMenuIndex={setSelectedMenuIndex}
          selectedMenuIndex={selectedMenuIndex}
        />
      )}
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          width: bodyContainerWidth,
          height: "100vh",
        }}
      >
        <TitleBar
          drawerWidth={drawerWidth}
          handleDrawerToggle={handleDrawerToggle}
          courseDetails={courseDetails}
          setCourseDetails={setCourseDetails}
          selectedMenuIndex={selectedMenuIndex}
          quiz={quiz}
          setQuiz={setQuiz}
        />
        <Divider />
        <Box sx={{ flex: 1 }}>
          <BodyContainer 
            courseDetails={courseDetails}
            setCourseDetails={setCourseDetails}
            selectedMenuIndex={selectedMenuIndex}
            setSelectedMenuIndex={setSelectedMenuIndex}
            quiz={quiz}
            setQuiz={setQuiz}
          />
        </Box>
        <Divider />
        <Footer drawerWidth={drawerWidth} />
      </Box>
    </Box>
  );
};

export default Landing;
