import React from "react";
import { Box, Typography, Button, Paper } from "@mui/material";

const Result = ({ qno, answerStatus, setQNo, setAnswerStatus, selectedMenuIndex, setCourseDetails, courseDetails }) => {
  const handleRetake = () => {
    setQNo(1);
    setAnswerStatus({});
  };

  const calculateScore = () => {
    const totalQuestions = 5;
    let correctCount = 0;
    Object.keys(answerStatus).forEach((key) => {
      if (answerStatus[key] === "correct") correctCount += 1;
    });
    const score = (correctCount * 100) / totalQuestions;
    return Math.ceil(score) > 100 ? 100 : Math.ceil(score);
  };

  const score = calculateScore();

  const generateScoreMessage = (score) => {
    if (score >= 80) {
      return {
        message: "Congratulations! You passed with an excellent score!",
        status: "pass",
        color: "green", // Green color for success
      };
    } else {
      return {
        message: "Sorry, you did not pass. Please try again.",
        status: "fail",
        color: "red", // Red color for failure
      };
    }
  };

  const { message, color } = generateScoreMessage(score);

  if (score >= 80 && !courseDetails.menuItems[selectedMenuIndex].quizCompleted) {
    setCourseDetails((prevState) => {
        const newState = {...prevState};
        newState.menuItems[selectedMenuIndex].quizCompleted = true;
        newState.menuItems[selectedMenuIndex].quizScore = score;
        newState.menuItems[selectedMenuIndex].progress = 100;
        return newState;
      });
  }

  return (
    <Box
      sx={{
        padding: 3,
        textAlign: "center",
        maxWidth: 400,
        margin: "auto",
        border: "1px solid #ccc",
        borderRadius: 2,
      }}
    >
      <Paper sx={{ padding: 3 }}>
        <Typography variant="h5" sx={{ marginBottom: 2 }}>
          Scorecard
        </Typography>
        <Typography variant="h6" sx={{ marginBottom: 2 }}>
          Your score: {score}%
        </Typography>
        <Typography
          variant="body1"
          sx={{
            fontWeight: "bold",
            color: color,
            marginBottom: 3,
          }}
        >
          {message}
        </Typography>
        {score < 80 && (
          <Button
            variant="contained"
            color="secondary"
            onClick={handleRetake}
            sx={{ marginTop: 2 }}
          >
            Retake
          </Button>
        )}
      </Paper>
    </Box>
  );
};

export default Result;
