import React, { useState, useEffect } from "react";
import { useDrag, useDrop } from "react-dnd";
import { DndProvider } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";
import { Box, Typography, Paper, Button, Grid } from "@mui/material";

// Define the list of questions and answers
const questions = [
  { id: 1, text: "1. Regulation of homeostasis" },
  { id: 2, text: "2. Health indicators" },
  { id: 3, text: "3. Immunological defence" },
  { id: 4, text: "4. Sensory functions" },
];

const answers = [
  { id: 1, text: "Elasticity reflecting overall health." },
  { id: 2, text: "Receptors for touch, pain, and temperature, helping us interact with the environment." },
  { id: 3, text: "Regulates temperature, water balance, and heat through blood vessels and sweat glands, controlling water absorption and evaporation." },
  { id: 4, text: "Protects against infections." },
];

// The MatchTheFollowing component
const Question5 = ({ qno, answerStatus, setQNo, setAnswerStatus }) => {
  const [matchedAnswers, setMatchedAnswers] = useState({
    1: null,
    2: null,
    3: null,
    4: null,
  });

  const handleDrop = (questionId, answerId) => {
    setMatchedAnswers((prevMatches) => ({
      ...prevMatches,
      [questionId]: answerId,
    }));
  };

  useEffect(() => {
    if (Object.keys(matchedAnswers).every(key => matchedAnswers[key] !== null)) {
      const allCorrect = Object.keys(matchedAnswers).every(key => isCorrectMatch(key, matchedAnswers[key]));
      setAnswerStatus((prevState) => ({ ...prevState, [qno]: allCorrect ? "correct": "wrong" }));
    }
}, [matchedAnswers])

  const handleNext = () => {
    setQNo(qno + 1);
  };

  const isCorrectMatch = (questionId, answerId) => {
    const correctMatches = {
      1: 3,
      2: 1,
      3: 4,
      4: 2,
    };
    return correctMatches[questionId] === answerId;
  };

  // Drag and Drop Item for Answers
  const DraggableAnswer = ({ answer }) => {
    const [{ isDragging }, drag] = useDrag(() => ({
      type: "ANSWER",
      item: { id: answer.id },
      collect: (monitor) => ({
        isDragging: monitor.isDragging(),
      }),
    }));

    return (
      <Paper
        ref={drag}
        sx={{
          padding: 2,
          marginBottom: 2,
          cursor: "move",
          backgroundColor: isDragging ? "#d3f8e2" : "#f5f5f5",
          boxShadow: 2,
        }}
      >
        <Typography variant="body1">{answer.text}</Typography>
      </Paper>
    );
  };

  // Drop Zone for Questions
  const DropZone = ({ question }) => {
    const [, drop] = useDrop(() => ({
      accept: "ANSWER",
      drop: (item) => handleDrop(question.id, item.id),
    }));

    return (
      <Paper
        ref={drop}
        sx={{
          padding: 2,
          marginBottom: 3,
          backgroundColor: matchedAnswers[question.id] ? "#d3ffd3" : "#e8e8e8",
          boxShadow: matchedAnswers[question.id] ? 3 : 1,
          minHeight: 100,
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
          transition: "all 0.2s ease",
        }}
      >
        <Typography variant="h6" sx={{ fontWeight: "bold" }}>
          {question.text}
        </Typography>
        {matchedAnswers[question.id] ? (
          <Typography variant="body2" sx={{ marginTop: 1 }}>
            Mapped Answer:{" "}
            {answers.find((ans) => ans.id === matchedAnswers[question.id])?.text}
          </Typography>
        ) : (
          <Typography variant="body2" sx={{ marginTop: 1, color: "gray" }}>
            Drop an answer here...
          </Typography>
        )}
      </Paper>
    );
  };

  return (
    <DndProvider backend={HTML5Backend}>
      <Box sx={{ padding: 3, display:'flex', flexDirection:'column', justifyContent:'flex-start' }}>
      <Typography variant="h6" sx={{ marginBottom: 2, fontWeight: 600 }}>
        4. Match the primary functions of the skin. 
      </Typography>
      <Typography variant="body1" sx={{ marginBottom: 2 }}>
        Drag and drop the function descriptions to their respective function names
      </Typography>
      <Box>
        <Grid container spacing={4}>
          {/* Questions Drop Zones */}
          <Grid item xs={6}>
            <Box>
              {questions.map((question) => (
                <DropZone key={question.id} question={question} />
              ))}
            </Box>
          </Grid>

          {/* Answers List */}
          <Grid item xs={6}>
            <Box>
              {answers.map((answer) => (
                <DraggableAnswer key={answer.id} answer={answer} />
              ))}
            </Box>
          </Grid>
        </Grid>
        </Box>

        <Box
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "flex-end",
          width: "100%",
        }}
      >
        <Button
          variant="contained"
          color="primary"
          onClick={handleNext}
          sx={{ marginTop: 2 }}
          disabled={!answerStatus[qno]}
        >
          Submit
        </Button>
      </Box>
      </Box>
    </DndProvider>
  );
};

export default Question5;
