import React, { useState, useRef, useEffect } from "react";
import Box from "@mui/material/Box";
import ActionBar from "./ActionBar";
import AllQuestions from "./questions/AllQuestions";
import Popup from "./Popup";

const BodyContainer = ({
  courseDetails,
  selectedMenuIndex,
  setCourseDetails,
  quiz,
  setQuiz
}) => {
  const videoRef = useRef(null);
  const [currentTime, setCurrentTime] = useState(0);
  const [showActivity, setShowActivity] = useState(0);
  const [activityCompleted, setActivityCompleted] = useState({});
  

  const handleVideoEnd = () => {
    setQuiz(true);
  };

  const handleTimeUpdate = () => {
    if (videoRef.current) {
      setCurrentTime(videoRef.current.currentTime);
    }
  };

  useEffect(() => {
    const video = videoRef?.current;
    if (video) {
      video.addEventListener("timeupdate", handleTimeUpdate);
      video.addEventListener("ended", handleVideoEnd);
    }
    return () => {
      if (video) {
        video.removeEventListener("timeupdate", handleTimeUpdate);
        video.removeEventListener("ended", handleVideoEnd);
      }
    };
  }, []);

  useEffect(() => {
    if (videoRef.current) {
      const subItemsProgress = {};
      let totalSectionProgress = courseDetails.menuItems[selectedMenuIndex].progress;
      courseDetails.menuItems[selectedMenuIndex]?.subItems?.forEach(({timeline, index}) => {
        if (currentTime >= timeline && currentTime < timeline+1) {
          subItemsProgress[timeline] = { progress: 100};
          if (!setActivityCompleted[timeline]) {
            setShowActivity(timeline);
            videoRef.current.pause();
          }
        }
      })
     
      // Calculate section level progress
      if (courseDetails.menuItems[selectedMenuIndex]?.subItems) {
        let totalSectionItemProgress = 0;
        let totalSectionItemCount = Object.keys(courseDetails.menuItems[selectedMenuIndex].timelineProgress).length;
        if (courseDetails.menuItems[selectedMenuIndex].quiz) totalSectionItemCount += 1;
        Object.keys(courseDetails.menuItems[selectedMenuIndex].timelineProgress).forEach(key => {
          totalSectionItemProgress += courseDetails.menuItems[selectedMenuIndex].timelineProgress[key].progress;
        })
        totalSectionProgress = (totalSectionItemProgress * 100) / (totalSectionItemCount * 100);
      }
      

      setCourseDetails((prevState) => {
        const newState = {...prevState};
        newState.menuItems[selectedMenuIndex].timelineProgress = {...newState.menuItems[selectedMenuIndex].timelineProgress, ...subItemsProgress};
        newState.menuItems[selectedMenuIndex].progress = Math.ceil(totalSectionProgress) > 100 ? 100 : Math.ceil(totalSectionProgress);
        return newState;
      });
    }
  }, [currentTime]);

  const showQuiz = () => {
    return courseDetails.menuItems[selectedMenuIndex].quiz && quiz && Object.keys(courseDetails.menuItems[selectedMenuIndex].timelineProgress).every(key => courseDetails.menuItems[selectedMenuIndex].timelineProgress[key].progress === 100);
  }

  const handlePopupProceed = () => {
    setActivityCompleted({ ...activityCompleted, [showActivity]: true });
    setShowActivity(0);
    videoRef.current.play();
  };

  useEffect(() => {
  }, [selectedMenuIndex])
  return (
    <Box
      component="main"
      sx={{
        left: 0,
        width: "100%",
        justifyContent: !showQuiz() ? 'center' : 'flex-start',
        alignItems: !showQuiz() ? 'center' : 'flex-start',
        display: "flex",
        flexDirection: "column",
        padding: 1,
      }}
    >
      {!showQuiz() &&  (<Box
        sx={{
          width: "100%",
          maxWidth: "100%",
          position: "relative",
          display: "flex",
          alignItems: "center",
          flexDirection: "column",
        }}
      >
        <video
          ref={videoRef}
          key={selectedMenuIndex}
          onTimeUpdate={handleTimeUpdate}
          style={{
            width: "80%",
            height: "auto",
            maxHeight: "75vh",
            maxWidth: "100%",
          }}
        >
          <source
            src={courseDetails.menuItems[selectedMenuIndex].videoContent}
            type="video/mp4"
          />
          Your browser does not support the video tag.
        </video>
        <ActionBar videoRef={videoRef} selectedMenuIndex={selectedMenuIndex} setCourseDetails={setCourseDetails} courseDetails={courseDetails} />
      </Box>)}
      {showQuiz() && (<AllQuestions selectedMenuIndex={selectedMenuIndex} setCourseDetails={setCourseDetails} courseDetails={courseDetails} />)}
      {!showQuiz() && (<Popup isDragDrop={true} isOpen={showActivity===234} handleProceed={handlePopupProceed} />)}
    </Box>
  );
};

export default BodyContainer;
