import React, { useState } from "react";
import Question1 from "./Question1";
import Question2 from "./Question2";
import Question3 from "./Question3";
import Question4 from "./Question4";
import Question5 from "./Question5";
import Result from "./Result"

const AllQuestions = ({ selectedMenuIndex, setCourseDetails, courseDetails }) => {
  const [qno, setQNo] = useState(1);
  const [answerStatus, setAnswerStatus] = useState({});
  return <div style={{width: '100%'}}>
  {qno===1 && <Question1 qno={qno} answerStatus={answerStatus} setQNo={setQNo} setAnswerStatus={setAnswerStatus} />}
  {qno===2 && <Question2 qno={qno} answerStatus={answerStatus} setQNo={setQNo} setAnswerStatus={setAnswerStatus} />}
  {qno===3 && <Question3 qno={qno} answerStatus={answerStatus} setQNo={setQNo} setAnswerStatus={setAnswerStatus} />}
  {qno===4 && <Question4 qno={qno} answerStatus={answerStatus} setQNo={setQNo} setAnswerStatus={setAnswerStatus} />}
  {qno===5 && <Question5 qno={qno} answerStatus={answerStatus} setQNo={setQNo} setAnswerStatus={setAnswerStatus} />}
  {qno===6 && <Result qno={qno} answerStatus={answerStatus} setQNo={setQNo} setAnswerStatus={setAnswerStatus} selectedMenuIndex={selectedMenuIndex} setCourseDetails={setCourseDetails} courseDetails={courseDetails} />}
    </div>
};

export default AllQuestions;
