import React, { useRef, useState, useEffect } from "react";
import { Box, IconButton, Typography, LinearProgress } from "@mui/material";
import PlayArrowIcon from "@mui/icons-material/PlayArrow";
import PauseIcon from "@mui/icons-material/Pause";
import Replay10Icon from "@mui/icons-material/Replay10";

const ActionBar = ({ videoRef, setCourseDetails, selectedMenuIndex, courseDetails }) => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTime, setCurrentTime] = useState(null);
  const [duration, setDuration] = useState(null);
  const progress = duration ? (currentTime / duration) * 100 : 0;
  useEffect(() => {
    // Update video duration when it's loaded
    if (videoRef.current) {
      setDuration(videoRef?.current?.duration ?? 0);
    }

    // Update current time periodically while playing
    const interval = setInterval(() => {
      if (videoRef.current && isPlaying) {
        setCurrentTime(videoRef.current.currentTime ?? 0);
      }
    }, 100);

    if (currentTime && duration && currentTime === duration && courseDetails.menuItems[selectedMenuIndex].progress !== 100) {
      setCourseDetails((prevState) => {
        const newState = {...prevState};
        newState.menuItems[selectedMenuIndex].progress = 100;
        return newState;
      });
    }

    return () => clearInterval(interval);
  });

  useEffect(() => {
    setIsPlaying(false);
    setCurrentTime(null);
    videoRef.current.currentTime = 0;
    if (!videoRef.current.paused) videoRef.current.pause();
  }, [selectedMenuIndex])

  const togglePlayPause = () => {
    if (videoRef.current.paused) {
      videoRef.current.play();
      setIsPlaying(true);
    } else {
      videoRef.current.pause();
      setIsPlaying(false);
    }
  };

  const formatTime = (seconds) => {
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = Math.floor(seconds % 60);
    if (isNaN(minutes) || isNaN(remainingSeconds)) return "0:00";
    return `${minutes}:${remainingSeconds < 10 ? "0" : ""}${remainingSeconds}`;
  };

  // Rewind 10 seconds functionality
  const rewind = () => {
    if (videoRef.current) {
      const newTime = Math.max(0, videoRef.current.currentTime - 10); // Ensure time doesn't go below 0
      videoRef.current.currentTime = newTime;
      setCurrentTime(newTime);
    }
  };

  if (currentTime && duration && currentTime === duration) {
    if (isPlaying) setIsPlaying(false);
  }

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "flex-start",
        width: "50%",
        height: "34px",
        borderRadius: "7px",
        backgroundColor: "#ecebeb",
        marginTop: "10px",
      }}
    >
      {/* Play/Pause button */}
      <IconButton onClick={togglePlayPause} sx={{flex:'0 0 5%'}}>
        {isPlaying ? <PauseIcon /> : <PlayArrowIcon />}
      </IconButton>

      {/* Rewind 10 seconds button */}
      <IconButton onClick={rewind} sx={{ flex: '0 0 5%' }}>
        <Replay10Icon />
      </IconButton>

      {/* Video Timer */}
      <Typography variant="body2" sx={{width: '70px', fontSize:{xs:'8px', sm:'10px', md: '10px'}, flex:{xs:'0 0 20%', sm:'0 0 15%', lg:'0 0 10%'}}}>
        {formatTime(currentTime)} / {formatTime(duration)}
      </Typography>

      {/* Linear Progress Bar */}
      <LinearProgress
        variant="determinate"
        value={progress}
        sx={{
          flex: 1,
          height: 8,
          borderRadius: 2,
          marginRight: "15px",
          backgroundColor: "#d3d3d3", // Track background color
          "& .MuiLinearProgress-bar": {
            backgroundImage: "linear-gradient(to right, #6f2595, #aa0061)", // Gradient color
          },
        }}
      />
    </Box>
  );
};

export default ActionBar;
