import React from "react";
import AppBar from "@mui/material/AppBar";
import MenuIcon from "@mui/icons-material/Menu";
import Typography from "@mui/material/Typography";
import IconButton from "@mui/material/IconButton";
import Toolbar from "@mui/material/Toolbar";
import { styled } from "@mui/material/styles";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import DialogContent from "@mui/material/DialogContent";
import CloseIcon from "@mui/icons-material/Close";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  "& .MuiDialogContent-root": {
    padding: theme.spacing(2),
    minWidth: "400px",
  },
  "& .MuiDialogActions-root": {
    padding: theme.spacing(1),
  },
}));

const TitleBar = ({ handleDrawerToggle, courseDetails, selectedMenuIndex, quiz }) => {
  const [open, setOpen] = React.useState(false);
  const getTimeLinesCompleted = () => {
    let count = 0;
    Object.keys(courseDetails.menuItems[selectedMenuIndex].timelineProgress).forEach(key => {
      if (courseDetails.menuItems[selectedMenuIndex].timelineProgress[key].progress === 100) count += 1;
    })
    return count >= Object.keys(courseDetails.menuItems[selectedMenuIndex].timelineProgress).length ? count - 1 : count;
  }

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  return (
    <AppBar
      position="relative"
      sx={{
        width: "100%",
        flex: "0 0 5%",
        backgroundColor: "white",
        boxShadow: "none",
        p: "5px",
        maxHeight: "50px",
        display: "flex",
        justifyContent: "center",
      }}
    >
      {/* SVG Linear Gradient Definition */}
      <svg width="0" height="0">
        <defs>
          <linearGradient id="gradient1" x1="0%" y1="0%" x2="100%" y2="100%">
            <stop
              offset="0%"
              style={{ stopColor: "#6f2595", stopOpacity: 1 }}
            />
            <stop
              offset="100%"
              style={{ stopColor: "#aa0061", stopOpacity: 1 }}
            />
          </linearGradient>
        </defs>
      </svg>
      <Toolbar>
        <IconButton
          color="inherit"
          aria-label="open drawer"
          edge="start"
          onClick={handleDrawerToggle}
          sx={{ mr: 2 }}
        >
          {/* MenuIcon with the linear gradient applied */}
          <MenuIcon
            sx={{
              fontSize: 25, // Adjust size of the icon as needed
              fill: "url(#gradient1)", // Apply the gradient to the fill of the icon
            }}
          />
        </IconButton>
        <Typography
          variant="h5"
          noWrap
          component="div"
          onClick={handleClickOpen}
          sx={{
            backgroundImage: "linear-gradient(to right, #6f2595, #aa0061)",
            backgroundClip: "text", // Ensures the gradient applies only to text
            color: "transparent", // Makes the text color transparent to show the gradient
            fontWeight: "bold",
            fontFamily: "Codec-Warm-ExtraBold, sans-serif",
            cursor: "pointer"
          }}
        >
          {courseDetails.menuItems[selectedMenuIndex].type==='single' ? courseDetails.menuItems[selectedMenuIndex].headerTitle : !quiz ? courseDetails?.menuItems[selectedMenuIndex]?.subItems[getTimeLinesCompleted()]?.headerTitle : 'Final Quiz'}
          <sup
            style={{ fontSize: "15px" }}
          >
            {courseDetails.menuItems[selectedMenuIndex].type==='single' || quiz ? '' : courseDetails?.menuItems[selectedMenuIndex]?.subItems[getTimeLinesCompleted()]?.supText}
          </sup>
        </Typography>
        <BootstrapDialog
          onClose={handleClose}
          aria-labelledby="customized-dialog-title"
          open={open}
        >
          <DialogTitle sx={{ m: 0, p: 2 }} id="customized-dialog-title">
            References
          </DialogTitle>
          <IconButton
            aria-label="close"
            onClick={handleClose}
            sx={(theme) => ({
              position: "absolute",
              right: 8,
              top: 8,
              color: theme.palette.grey[500],
            })}
          >
            <CloseIcon
              sx={{
                fontSize: 40, // Adjust size of the icon as needed
                fill: "url(#gradient1)", // Apply the gradient to the fill of the icon
              }}
            />
          </IconButton>
          <DialogContent dividers>
            <List sx={{ width: "100%", minWidth: 360 }}>
              {courseDetails.menuItems[selectedMenuIndex].type==='multiple' && courseDetails?.menuItems[selectedMenuIndex]?.subItems[getTimeLinesCompleted()]?.reference.map(
                (ref, index) => {
                  return (
                    <ListItem key={1}>
                      <ListItemText primary={`${index + 1}. ${ref} `} />
                    </ListItem>
                  );
                }
              )}
            </List>
          </DialogContent>
        </BootstrapDialog>
      </Toolbar>
    </AppBar>
  );
};

export default TitleBar;
