import React, { useState } from "react";
import Box from "@mui/material/Box";
import CssBaseline from "@mui/material/CssBaseline";
import Divider from "@mui/material/Divider";
import Drawer from "@mui/material/Drawer";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import ListItemButton from "@mui/material/ListItemButton";
import Toolbar from "@mui/material/Toolbar";
import LinearProgress from "@mui/material/LinearProgress";
import Typography from "@mui/material/Typography";
import { Accordion, AccordionSummary, AccordionDetails } from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import CheckIcon from "@mui/icons-material/Check";
import CircularProgressBarLabel from "./CircularProgressBarLabel";

const menuHeaderStyle = {
  "& .MuiTypography-root": {
    width: "150px",
    fontFamily: "Codec-Warm-Regular, sans-serif", // Custom font-family for Typography
    fontWeight: "600", // Font weight for typography
    fontSize: "18px",
  },
};

const menuItemStyle = {
  "& .MuiTypography-root": {
    width: "130px",
    fontFamily: "Codec-Warm-Regular, sans-serif",
    fontWeight: "600",
    fontSize: "15px",
  },
};

const Navbar = ({
  drawerWidth,
  open,
  setCourseDetails,
  courseDetails,
  selectedMenuIndex,
  setSelectedMenuIndex,
}) => {
  const [expanded, setExpanded] = useState(null);

  const handleMenuItemClick = (index, isExpanded) => {
    setSelectedMenuIndex(index);
    if (isExpanded) setExpanded(expanded === index ? "" : index);
  };

  const getModuleProgress = (details) => {
    const moduleCount = details.menuItems.length;
    let currentTotalAllItemsProgress = 0;
    details.menuItems.forEach((item) => {
      currentTotalAllItemsProgress += item.progress;
    });
    const currentTotalModuleProgress =
      (currentTotalAllItemsProgress * 100) / (moduleCount * 100);
    return Math.ceil(currentTotalModuleProgress) > 100
      ? 100
      : Math.ceil(currentTotalModuleProgress);
  };

  const circularCheckIcon = (
    <Box
      sx={{
        flexBasis: "20px",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        backgroundColor: "#AA0061", // Circular background color
        borderRadius: "50%", // Makes the container circular
        width: "20px", // Set the size of the circle
        height: "20px", // Set the size of the circle
        color: "white", // Icon color
      }}
    >
      <CheckIcon sx={{ fontSize: 13 }} />
    </Box>
  );

  const renderMenuItem = (details, index) => {
    if (details.type === "single") {
      return (
        <>
          <List>
            <ListItem disablePadding>
              <ListItemButton
                onClick={() => handleMenuItemClick(index)}
                sx={{
                  borderLeft:
                    selectedMenuIndex === index ? "4px solid #6f2595" : "none", // Blue vertical bar
                  backgroundColor:
                    selectedMenuIndex === index
                      ? "rgba(111, 37, 149, 0.1)"
                      : "transparent",
                }}
              >
                <Box
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <ListItemText
                    primary={details.displayName}
                    sx={menuHeaderStyle}
                  />
                  <CircularProgressBarLabel
                    variant="determinate"
                    size={30}
                    value={details.progress}
                  />
                </Box>
              </ListItemButton>
            </ListItem>
          </List>
          <Divider />
        </>
      );
    } else if (details.type === "multiple") {
      return (
        <>
          <List>
            <Accordion
              expanded={expanded === index}
              onChange={() => {
                handleMenuItemClick(index, true);
              }}
              sx={{
                boxShadow: "none",
              }}
            >
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
                sx={{
                  borderLeft:
                    selectedMenuIndex === index ? "4px solid #6f2595" : "none",
                  backgroundColor:
                    selectedMenuIndex === index
                      ? "rgba(111, 37, 149, 0.1)"
                      : "transparent",
                  paddingLeft: "16px",
                }}
              >
                <Box
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <ListItemText
                    primary={details.displayName}
                    sx={menuHeaderStyle}
                  />
                  <CircularProgressBarLabel
                    variant="determinate"
                    size={30}
                    value={details.progress}
                  />
                </Box>
              </AccordionSummary>
              <AccordionDetails>
                <List>
                  {details.subItems.map((item) => {
                    const itemTimeline = item.timeline;
                    return (
                      <ListItem button>
                        <Box
                          sx={{
                            display: "flex",
                            width: "100%",
                            alignItems: "center",
                            justifyContent: "flex-start",
                          }}
                        >
                          <ListItemText
                            primary={item.displayName}
                            sx={menuItemStyle}
                          />
                          {details.timelineProgress[itemTimeline].progress ===
                          100
                            ? circularCheckIcon
                            : ""}
                        </Box>
                      </ListItem>
                    );
                  })}
                  {details.quiz && (
                    <ListItem button>
                      <Box
                        sx={{
                          display: "flex",
                          width: "100%",
                          alignItems: "center",
                          justifyContent: "flex-start",
                        }}
                      >
                        <ListItemText primary="Final Quiz" sx={menuItemStyle} />
                        {details.quizCompleted ? circularCheckIcon : ""}
                      </Box>
                    </ListItem>
                  )}
                </List>
              </AccordionDetails>
            </Accordion>
          </List>
          <Divider />
        </>
      );
    }
  };

  const drawer = (
    <div>
      <Toolbar sx={{ height: "200px", p: "20px" }}>
        <Box
          sx={{
            width: "100%",
            height: "100%",
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
          }}
        >
          <Box sx={{ flex: 1, display: "flex", alignItems: "center" }}>
            <Typography
              variant="h5"
              component="div"
              sx={{
                backgroundImage: "linear-gradient(to right, #6f2595, #aa0061)",
                backgroundClip: "text",
                //color: "transparent",
                color: "black",
                fontWeight: "bold",
                fontFamily: "Codec-Warm-ExtraBold, sans-serif",
              }}
            >
              Skin Fundamentals Module-1
            </Typography>
          </Box>
          <Box sx={{ flexBasis: "20%" }}>
            <LinearProgress
              variant="determinate"
              value={getModuleProgress(courseDetails)}
              sx={{
                backgroundColor: "#d3d3d3", // Track background color
                "& .MuiLinearProgress-bar": {
                  backgroundImage:
                    "linear-gradient(to right, #6f2595, #aa0061)", // Gradient color
                },
              }}
            />
            <Typography variant="caption" noWrap component="div">
              {`${getModuleProgress(courseDetails)}% completed`}
            </Typography>
          </Box>
        </Box>
      </Toolbar>
      <Divider />
      {courseDetails.menuItems.map((menuItem, index) =>
        renderMenuItem(menuItem, index)
      )}
    </div>
  );

  return (
    <Box
      sx={{
        display: "flex",
        boxShadow: "4px 0 12px rgba(0, 0, 0, 0.15)",
        zIndex: 1010,
      }}
    >
      <CssBaseline />
      <Box
        component="nav"
        sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
        aria-label="course-content"
      >
        {/* <Drawer
          variant="temporary"
          open={mobileOpen}
          onTransitionEnd={handleDrawerTransitionEnd}
          onClose={handleDrawerClose}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: "block", sm: "none" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: drawerWidth,
            },
          }}
        >
          {drawer}
        </Drawer> */}
        <Drawer
          variant="persistent"
          anchor="left"
          sx={{
            display: "block",
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: drawerWidth,
            },
          }}
          open={open}
        >
          {drawer}
        </Drawer>
      </Box>
    </Box>
  );
};

export default Navbar;
