import React from "react";
import Box from "@mui/material/Box";
import { Typography, Button } from "@mui/material";
import Q1Image from "../../assets/Q1.jpg";

const Question4 = ({ qno, answerStatus, setQNo, setAnswerStatus }) => {
  const image = Q1Image;

  const handleOverlayClick = (answer) => {
    if (answer === "correct") {
      setAnswerStatus((prevState) => ({ ...prevState, [qno]: "correct" }));
    } else if (answer === "wrong") {
      setAnswerStatus((prevState) => ({ ...prevState, [qno]: "wrong" }));
    }
  };

  const handleNext = () => {
    setQNo(qno + 1);
  };

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "flex-start",
        padding: 2,
        width: "100%",
      }}
    >
      <Typography variant="h6" sx={{ marginBottom: 2, fontWeight: 600 }}>
        4. In which layer of the skin are keratinocytes present?
      </Typography>
      <Typography variant="body1" sx={{ marginBottom: 2 }}>
        From the three options labelled in the diagram below. Click box and
        select the relevant layer.
      </Typography>
      <Box
        sx={{
          position: "relative", // Make the container relative for overlay positioning
          width: "100%", // Make image responsive
          maxWidth: 400, // Max width for large screens
        }}
      >
        <Box
          component="img"
          src={image}
          alt={image}
          sx={{
            width: "100%", // Responsive image width
            height: "auto", // Maintain aspect ratio
            borderRadius: "8px",
          }}
        />
        <Box
          onClick={() => handleOverlayClick("correct")}
          sx={{
            position: "absolute",
            top: 100,
            right: 0,
            width: "25%",
            height: "10%",
            background: "rgba(0, 0, 0, 0)", // Transparent overlay
            cursor: "pointer",
            //backgroundColor: "blue",
            //opacity: "0.5",
            border: "5px solid #aa0061",
          }}
        />
        <Box
          onClick={() => handleOverlayClick("wrong")}
          sx={{
            position: "absolute",
            top: 160,
            right: 0,
            width: "25%",
            height: "10%",
            background: "rgba(0, 0, 0, 0)", // Transparent overlay
            cursor: "pointer",
            //backgroundColor: "red",
            //opacity: "0.5",
            border: "5px solid #aa0061",
          }}
        />
        <Box
          onClick={() => handleOverlayClick("wrong")}
          sx={{
            position: "absolute",
            top: 215,
            right: 0,
            width: "25%",
            height: "10%",
            background: "rgba(0, 0, 0, 0)", // Transparent overlay
            cursor: "pointer",
            // backgroundColor: "yellow",
            // opacity: "0.5",
            border: "5px solid #aa0061",
          }}
        />
      </Box>

      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "flex-end",
          width: "100%",
        }}
      >
        <Button
          variant="contained"
          color="primary"
          onClick={handleNext}
          sx={{ marginTop: 2 }}
          disabled={!answerStatus[qno]}
        >
          Next
        </Button>
      </Box>
    </Box>
  );
};

export default Question4;
