import React from "react";
import Box from "@mui/material/Box";
import { Typography, Button } from "@mui/material";
import Q1Image from "../../assets/Q1.jpg";

const Question1 = ({ qno, answerStatus, setQNo, setAnswerStatus }) => {
  const image = Q1Image;

  const handleOverlayClick = (answer) => {
    if (answer === "correct") {
      setAnswerStatus((prevState) => ({ ...prevState, [qno]: "correct" }));
    } else if (answer === "wrong") {
      setAnswerStatus((prevState) => ({ ...prevState, [qno]: "wrong" }));
    }
  };

  const handleNext = () => {
    setQNo(qno + 1);
  };

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "flex-start",
        padding: 2,
        width: "100%",
      }}
    >
      <Typography variant="h6" sx={{ marginBottom: 2, fontWeight: 600 }}>
        1. The primary roles of this particular layer of the skin include
        temperature regulation, supplying the epidermis with nutrient saturated
        blood and hosting most of the skin's specialised cells and structures
        that perform essential specialised functions in the skin.
      </Typography>
      <Typography variant="h6" sx={{ marginBottom: 2 }}>
        Which layer of the skin corresponds to the description above?
      </Typography>
      <Box
        sx={{
          position: "relative", // Make the container relative for overlay positioning
          width: "100%", // Make image responsive
          maxWidth: 400, // Max width for large screens
        }}
      >
        <Box
          component="img"
          src={image}
          alt={image}
          sx={{
            width: "100%", // Responsive image width
            height: "auto", // Maintain aspect ratio
            borderRadius: "8px",
          }}
        />
        <Box
          onClick={() => handleOverlayClick("wrong")}
          sx={{
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            height: "100%",
            background: "rgba(0, 0, 0, 0)", // Transparent overlay
            cursor: "pointer",
            // backgroundColor: "red",
            // opacity: "0.5",
          }}
        />
        {/* Overlay div on top of the image */}
        <Box
          onClick={() => handleOverlayClick("correct")}
          sx={{
            position: "absolute",
            top: 100,
            left: 0,
            width: "100%",
            height: "30%",
            background: "rgba(0, 0, 0, 0)", // Transparent overlay
            cursor: "pointer",
            // backgroundColor: "yellow",
            // opacity: "0.5",
          }}
        />
      </Box>

      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "flex-end",
          width: "100%",
        }}
      >
        <Button
          variant="contained"
          color="primary"
          onClick={handleNext}
          sx={{ marginTop: 2 }}
          disabled={!answerStatus[qno]}
        >
          Next
        </Button>
      </Box>
    </Box>
  );
};

export default Question1;
